import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
import FinancingItem from '../FormItem/financingItem.jsx'
import InvestItem from '../FormItem/investItem.jsx'
import DeadlineItem from '../FormItem/deadlineItem.jsx'
import MortgageItem from '../FormItem/mortgageItem.jsx'
export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "key": "",      //编号
            "title": "",    //标题
            "category": "",         //类别
            "type": "",        //投资类型
            "industry": "",    //投资行业
            "region": "",     //投资地区
            "financingProfits": {
                "isNeed": "",
                "number": ""
            },     //融资方利润
            "invest": {     //单笔投资区间
                "min": "",
                "max": ""
            },
            "deadline": {   //期限
                "min": "",
                "max": ""
            },
            "minRate" : "",     //最低接受利率
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "rate": {       //折扣率
                    "min": "",
                    "max": ""
                }
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "needCost": "",        //前期费用
            "detail": "",   //详情
            "accessory": {        //附件
                "name": "",
                "link": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/fund/normal/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/fund/normal/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("审核成功!", 1, function() {
                                hashHistory.push("/fund/normal")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金标题"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("title", {
                            rules: [{require: true, message: "请输入标题!"}],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.props.actionType=="detail" ? true : false} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金类别"
                    >
                        {getFieldDecorator("category", {
                            initialValue: "1"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="3">资金收购</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资类型"
                    >
                        <Select disabled value={this.state.type}>
                            {
                                this.state.assetsType.debts.map(function(item) {
                                    return <Option value={item.key} key={item.key}>{item.value}</Option>
                                })
                            }
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资行业"
                    >
                        {getFieldDecorator("industry", {
                            initialValue: this.state.industry
                        })(
                            <Select disabled>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资地区"
                    >
                        {getFieldDecorator("region", {
                            initialValue: this.state.region
                        })(
                            <Cascader disabled
                                options={this.state.cascaderRegion}
                            />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方去年净利润"
                    >
                        {getFieldDecorator("financingProfits", {
                            initialValue: {
                                isNeed: this.state.financingProfits.isNeed,
                                number: this.state.financingProfits.number }
                        })(<FinancingItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="单笔投资区间"
                    >

                        {getFieldDecorator("invest", {
                            initialValue: {
                                min: this.state.invest.min,
                                max: this.state.invest.max
                            }
                        })(<InvestItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="期限"
                    >

                        {getFieldDecorator("deadline", {
                            initialValue: {
                                min: this.state.deadline.min,
                                max: this.state.deadline.max
                            }
                        })(<InvestItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="最低接受利率"
                    >
                        <Input disabled value={this.state.minRate} />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="是否必须有抵押物"
                    >
                        <RadioGroup disabled value={this.state.mortgage.isNeed}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物类型"
                    >
                        <CheckboxGroup disabled value={this.state.mortgage.type}>
                            <Checkbox value="1">固定资产</Checkbox>
                            <Checkbox value="2">有价证券</Checkbox>
                            <Checkbox value="3">其他</Checkbox>
                        </CheckboxGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="抵/质押物折扣率"
                    >
                        {getFieldDecorator("invest", {
                            initialValue: {
                                min: this.state.mortgage.rate.min,
                                max: this.state.mortgage.rate.max
                            }
                        })(<MortgageItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="是否必须要担保"
                    >
                        <RadioGroup disabled value={this.state.guarantee.isNeed}>
                            <Radio value="1">是</Radio>
                            <Radio value="0">否</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="担保方式"
                    >
                        <Input disabled value={this.state.guarantee.method} />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="前期费用"
                    >
                        <RadioGroup disabled value={this.state.needCost}>
                            <Radio value="1">有(评估/担保等)</Radio>
                            <Radio value="0">没有</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="资金详情"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("detail", {
                            rules: [{require: true, message: "请输入资金详情!"}],
                            initialValue: this.state.detail
                        })(
                            <TextArea disabled={this.props.actionType == "detail" ? true : false} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.accessory.link}>{this.state.accessory.name}</a>
                        </span>
                    </FormItem>
                    {
                        this.props.actionType == "audit" ?
                        <FormItem
                            required
                            {...formItemLayout}
                            label="审核状态"
                            hasFeedback={this.props.actionType=="audit" ? true : false}
                        >
                            {getFieldDecorator("audit.status", {
                                rules: [{required: true,message:"请选择审核状态!"}],
                                initialValue: "0"
                            })(
                                <RadioGroup>
                                    <Radio value="0">不通过</Radio>
                                    <Radio value="1">通过</Radio>
                                </RadioGroup>
                            )}
                        </FormItem> : ""
                    }
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                {...formItemLayout}
                                label="审核意见"
                            >
                                {getFieldDecorator("audit.comment", {
                                    initialValue: this.state.auditComment
                                })(
                                    <TextArea autosize={{ minRows: 3 }} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            this.props.actionType == "audit" ? <Button type="primary" htmlType="submit" style={{"marginRight": "20px"}}>提交</Button> : ""
                        }
                        {
                            this.props.actionType == "audit" ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


