import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import ProfitsItem from '../FormItem/financingItem.jsx'
import InvestItem from '../FormItem/investItem.jsx'
import BenefitsItem from '../FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "key": "",
            "title": "",
            "category": "",
            "stockType": "",
            "type": "",        //投资类型
            "industry": "",
            "region": "",
            "companyProfits": {
                "isNeed": "",
                "number": ""
            },
            invest: {
                min: "",
                max: ""
            },
            "estimateBenefits": {
                "times": "",
                "type": ""
            },
            "needCost": "1",
            "detail": "",
            "accessory": {
                "name": "",
                "link": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/fund/normal/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/fund/normal/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("审核成功!", 1, function() {
                                hashHistory.push("/fund/normal")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金标题"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("title", {
                            rules: [{require: true, message: "请输入标题!"}],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.props.actionType=="detail" ? true : false} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金类别"
                    >
                        <Select disabled value="2">
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="3">资金收购</Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="参/控股"
                    >
                        <RadioGroup disabled value={this.state.stockType}>
                            <Radio value="1">参股型投资</Radio>
                            <Radio value="2">控股型投资</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资类型"
                    >
                        <Select disabled value={this.state.type}>
                            {
                                this.state.assetsType.stock.map(function(item) {
                                    return <Option value={item.key} key={item.key}>{item.value}</Option>
                                })
                            }
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资行业"
                    >
                        <Select disabled value={this.state.industry}>
                            {
                                this.state.industryList.map(function(item) {
                                    return <Option value={item.key} key={item.key}>{item.value}</Option>
                                })
                            }
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资地区"
                    >
                        <Cascader disabled value={this.state.region} options={this.state.cascaderRegion}
                        />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="标的企业去年利润"
                    >
                        {getFieldDecorator("companyProfits", {
                            initialValue: {
                                isNeed: this.state.companyProfits.isNeed,
                                number: this.state.companyProfits.number }
                        })(<ProfitsItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="单笔投资区间"
                    >

                        {getFieldDecorator("invest", {
                            initialValue: {
                                min: this.state.invest.min,
                                max: this.state.invest.max
                            }
                        })(<InvestItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资估值倍数"
                    >

                        {getFieldDecorator("estimateBenefits", {
                            initialValue: {
                                times: this.state.estimateBenefits.times,
                                type: this.state.estimateBenefits.type
                            }
                        })(<BenefitsItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="前期费用"
                    >
                        <RadioGroup disabled value={this.state.needCost}>
                            <Radio value="1">有(评估/担保等)</Radio>
                            <Radio value="0">没有</Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="资金详情"
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("detail", {
                            rules: [{require: true, message: "请输入资金详情!"}],
                            initialValue: this.state.detail
                        })(
                            <TextArea disabled={this.props.actionType == "detail" ? true : false} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.accessory.link}>{this.state.accessory.name}</a>
                        </span>
                    </FormItem>
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label="审核状态"
                                hasFeedback={this.props.actionType=="audit" ? true : false}
                            >
                                {getFieldDecorator("audit.status", {
                                    rules: [{required: true,message:"请选择审核状态!"}],
                                    initialValue: "0"
                                })(
                                    <RadioGroup>
                                        <Radio value="0">不通过</Radio>
                                        <Radio value="1">通过</Radio>
                                    </RadioGroup>
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                {...formItemLayout}
                                label="审核意见"
                            >
                                {getFieldDecorator("audit.comment", {
                                    initialValue: this.state.auditComment
                                })(
                                    <TextArea autosize={{ minRows: 3 }} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            this.props.actionType == "audit" ? <Button type="primary" htmlType="submit" style={{"marginRight": "20px"}}>提交</Button> : ""
                        }
                        {
                            this.props.actionType == "audit" ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


